import { Row, Col, Card, CardBody, CardTitle, Button, CardImg } from "reactstrap";
import { useNavigate } from "react-router-dom";
import TitleCard from "./TitleCard";

const About = () => {
  let navigate = useNavigate();

  return (
    <Row className="about">
      <i onClick={() => navigate("/starter")} class="bi bi-arrow-left-short back-button"></i>
      <TitleCard />
      <Col className="about-content">
        {/* --------------------------------------------------------------------------------*/}
        {/* Card-1*/}
        {/* --------------------------------------------------------------------------------*/}
        <Card>
          <CardTitle tag="h6" className="border-bottom p-3 mb-0">
            {/* <i className="bi bi-bell me-2"> </i> */}
            <i class="bi bi-person-fill"></i> About the author
          </CardTitle>
          <CardBody >
            <Row justify-content>
              <Col >
                <div style={{ textAlign: 'center' }}>
                  <CardImg style={{ width: '125px', marginBottom: '10px' }} alt="Card image cap" src={'/author.jpg'} />
                </div>

                {/* <h2 >Dr.Braivin Camly</h2> */}
                <p style={{ textAlign: 'justify', fontSize: '14px' }} >
                  <span style={{ fontWeight: '600' }}>Dr. BRAIVIN CAMLY R</span>,
                  presently working as a Professor in the Department of Samhita, Siddhanta and Sanskrit, Govt. Ayurveda College, Thiruvananthapuram, Kerala has completed her BAMS degree from Govt. Ayurveda College, Thiruvananthapuram in the year 2002. She did her post-graduation in Ayurveda Siddhanta from Govt. Ayurveda College, Thiruvananthapuram in 2011. She is pursuing Ph.D. at Parul Institute of Ayurveda, Parul University, Vadodara, Gujarat under the guidance of Dr. Akshar Ashok Kulkarni MD (Ay), Ph.D., Professor, Department. of Samhita, Siddhanta and Sanskrit, Parul Institute of Ayurveda, Parul University, Vadodara, Gujarat.
                </p>
                <p style={{ textAlign: 'justify', fontSize: '14px' }}  >
                  She has attended sizeable National and International conferences, presented many papers in National and International seminars and published a few scientific papers in National and International journals.
                </p>
                <p style={{ textAlign: 'justify', fontSize: '14px' }}  >
                  She is working as a teaching faculty in the department of Samhita, Siddhanta and Sanskrit, Govt. Ayurveda College, Thiruvananthapuram, Kerala since February 2006.
                </p>
              </Col>
            </Row>
          </CardBody>
        </Card>
      </Col>
    </Row>
  );
};

export default About;
